import React from 'react';
import './menuBar.css';

export class MenuBar extends React.Component {
    constructor(props){
        super(props);
        this.checkActive=this.checkActive.bind(this)
      }

    checkActive(contentType){
        if (this.props.content === contentType) {
            return 'active';
        } else {
            return '';
        };
    }

    renderMenuBar(){
        return Object.keys(this.props.contentTypes).map(contentType => {
            let contentTypeValue = this.props.contentTypes[contentType];
            return (
                <li 
                    className={this.checkActive(contentTypeValue)} 
                    key={contentTypeValue}
                    onClick={(function(){
                        this.props.onClickMenu(contentTypeValue)
                    }.bind(this))}
                >
                    {contentType}
                </li>
            )
        });
    }


    render(){
        return(
            <div className='menuBar'>
                <ul>
                    {this.renderMenuBar()}
                </ul>
            </div> 
        )
    }
}