import React from 'react';
import './header.css';

export class Header extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            position: 1,
        };
        this.handleClick = this.handleClick.bind(this);
    }

    //     handleClick(){
    //     let {position} = this.state;
    //     position++;
    //     if (position > 3) {
    //         position = 1
    //     };
    //     this.setState(
    //         {position}
    //     );


    handleClick(){
        let i = this.state.position + 1;
        this.setState(
            {position: i}
        );
        
    }

    render(){
        return(
            <div className="app-header">
                {(this.state.position === 1 || this.state.position > 4)  && (
                    <div className="app-header__text-1" onClick={this.handleClick}>
                        YOUR DREAM <br />JUNIOR <br />FRONTEND DEVELOPER
                    </div>
                )}
                {this.state.position === 2 && (
                    <div className="app-header__text-2" onClick={this.handleClick}>
                        PEOPLE RARELY SUCCEED <br />UNLESS THEY HAVE FUN IN WHAT THEY ARE DOING
                    </div>
                )}
                {this.state.position === 3 && (
                    <div className="app-header__text-3" onClick={this.handleClick}>
                        THE BEST WAY OUT IS ALWAYS THROUGH
                    </div>
                )}
                {this.state.position === 4 && (
                    <div className='crash'>
                        <img src='/blobby.jpg' className="crash__img" />
                        <div className="app-header__text-4">
                            oh...<br />
                            it seems oh..<br />
                            u've ruined this page<br /><br />
                            <span onClick={this.handleClick}>fix it</span>
                        </div>
                        
                    </div>
                )}

            </div>
        )
    }
}

